	module.exports = {"pathContext":{"group":[{"node":{"id":"1c43da6c-2bb6-5239-aac8-8fdeef221c2a","slug":"women-catching-up-with-men-in-alcohol-consumption-and-its-associated-harms","title":"Women catching up with men in alcohol consumption and its associated harms","excerpt":"<p>Trend most evident among young adults, international analysis shows Women are catching up with men in terms of their alcohol consumption and its impact on their health, finds an analysis of the available international evidence, spanning over a century and published in the online journal BMJ Open. The trend, known as ‘sex convergence’, is most evident [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/10/24/women-catching-up-with-men-in-alcohol-consumption-and-its-associated-harms/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 24, 2016","template":"","format":"standard"}},{"node":{"id":"693f4bf2-9e40-5d96-9785-b7a69cc54568","slug":"most-read-articles-in-september","title":"Most read articles in September: young people’s views on school sex education, gender equity in academic medicine and improving patient safety","excerpt":"<p>September’s most read list sees several new entries as well as some popular non-movers. This month’s top spot still belongs to Ravnskov et al and their systematic review on the associations between low-density-lipoprotein cholesterol and mortality in the elderly. Pound et al come in at number three with a qualitative synthesis of young people&#8217;s views and [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/10/07/most-read-articles-in-september/\">Read More&#8230;</a></p>\n","status":"publish","date":"October 07, 2016","template":"","format":"standard"}},{"node":{"id":"acc0dd35-7be3-5a02-bc22-cbc8e0cea007","slug":"guest-blog-improving-peer-review-using-peer-reviewed-studies","title":"Guest blog: Improving peer review using peer-reviewed studies #PeerRevWk16","excerpt":"<p>This week is the second ever “peer review week”. The theme for this year is &#8220;Recognition for Review&#8221;. Peer review week aims to highlight the importance of peer review, which is a crucial part of the research process. We asked Dr Adrian Barnett, from the Queensland University of Technology, and a member of our editorial board, to survey [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/09/19/guest-blog-improving-peer-review-using-peer-reviewed-studies/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 19, 2016","template":"","format":"standard"}},{"node":{"id":"d7c46d72-c02d-5d0e-b2c4-525799ef7292","slug":"school-sex-education-often-negative-heterosexist-and-out-of-touch","title":"School sex education often negative, heterosexist, and out of touch","excerpt":"<p>And taught by poorly trained, embarrassed teachers, say young people School sex education is often negative, heterosexist, and out of touch, and taught by poorly trained, embarrassed teachers, finds a synthesis of the views and experiences of young people in different countries, published in the online journal BMJ Open. Schools’ failure to acknowledge that sex education [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/09/12/school-sex-education-often-negative-heterosexist-and-out-of-touch/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 12, 2016","template":"","format":"standard"}},{"node":{"id":"596da3d8-3707-53e3-99f7-94d5792b315d","slug":"bmj-open-to-publish-abstracts-for-the-ucl-qualitative-health-research-network-symposium","title":"BMJ Open to publish abstracts for the UCL Qualitative Health Research Network symposium","excerpt":"<p>On the Tuesday 7th February 2017, the UCL Qualitative Health Research Network will be hosting their third symposium entitled ‘Engagement, Co-production, and Collaborative Meaning-Making: Collaboration in Qualitative Health Research’, supported by The Wellcome Trust. Held within the Institute of Child Health, 30 Guildford Street, London, the Network invite all those with an interest in qualitative health [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/09/09/bmj-open-to-publish-abstracts-for-the-ucl-qualitative-health-research-network-symposium/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 09, 2016","template":"","format":"standard"}},{"node":{"id":"be63a9b4-1afd-514b-92dd-7ec7d31aaffa","slug":"volunteering-and-mental-health-breast-feeding-outcomes-and-condom-use-intentions-most-read-articles-in-august","title":"Volunteering and mental health, breast feeding outcomes and condom use intentions: Most read articles in August","excerpt":"<p>Association of volunteering and mental well-being, breast feeding outcomes and place of birth, and condom use intentions of heterosexual men The August most read list contains papers with a number of different study designs, on a number of different topics. Returning to the top spot this month is a systematic review on the lack of [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/09/07/volunteering-and-mental-health-breast-feeding-outcomes-and-condom-use-intentions-most-read-articles-in-august/\">Read More&#8230;</a></p>\n","status":"publish","date":"September 07, 2016","template":"","format":"standard"}},{"node":{"id":"65b08267-0751-5af6-a225-475289ae97be","slug":"complimentary-therapies-wellbeing-and-consultants-as-victims-of-bullying-most-read-articles-in-july","title":"Complimentary therapies, well-being and consultants as victims of bullying: Most read articles in July.","excerpt":"<p>  July’s top 10 most read papers sees two studies taking into account doctors’ points of view. The first, by Bourne et al., takes a closer look at look at which aspects of the complaints process they find the most stressful. The second, by Shabazz et al., uses a survey of Royal College of Obstetricians [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/08/11/complimentary-therapies-wellbeing-and-consultants-as-victims-of-bullying-most-read-articles-in-july/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 11, 2016","template":"","format":"standard"}},{"node":{"id":"508a74f0-0f39-5d45-867a-58bb2190aa5b","slug":"volunteering-in-middle-age-and-senior-years-linked-to-enhanced-mental-health","title":"Volunteering in middle age and senior years linked to enhanced mental health","excerpt":"<p>No positive association seen before age of 40, suggesting link varies across life course Volunteering in middle and older age is linked to good mental health/emotional wellbeing, finds a large study of British adults, published in the online journal BMJ Open. But no such association was seen before the age of 40, suggesting that the [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/08/08/volunteering-in-middle-age-and-senior-years-linked-to-enhanced-mental-health/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 08, 2016","template":"","format":"standard"}},{"node":{"id":"43463fba-eaf5-5810-94fb-e60bb85f284c","slug":"bmj-open-works-with-publons-to-give-credit-for-peer-review","title":"BMJ Open works with Publons to give credit for peer review","excerpt":"<p>We’re pleased to announce that BMJ Open has partnered with Publons to help reviewers gain credit for their work. Publons is a free service for reviewers, which enables them to gain public recognition for the reviews they complete. To take advantage of this service, you will first need to create a profile on Publons. When you [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/08/01/bmj-open-works-with-publons-to-give-credit-for-peer-review/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 01, 2016","template":"","format":"standard"}},{"node":{"id":"fc65ff75-203b-5a7e-90b0-d7502a28896b","slug":"top-10-most-read-cholesterol-and-mortality-in-the-elderly-chronic-pain-in-the-uk-and-smokers-quitting-attempts","title":"Top 10 Most Read: Cholesterol and mortality in the elderly, chronic pain in the UK and smokers’ quitting attempts","excerpt":"<p>Several new entries make it into our top 10 Most Read list this month. In first place is a systematic review on the associations between low-density-lipoprotein cholesterol and mortality in the elderly. Further down the list is an analysis of Italian medical societies’ websites to examine conflicts of interest between professional medical societies and industry. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2016/07/08/top-10-most-read-cholesterol-and-mortality-in-the-elderly-chronic-pain-in-the-uk-and-smokers-quitting-attempts/\">Read More&#8230;</a></p>\n","status":"publish","date":"July 08, 2016","template":"","format":"standard"}}],"pathPrefix":"posts","first":false,"last":false,"index":6,"pageCount":26,"additionalContext":{}}}

